.tab {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    flex-grow: 1;
    flex-shrink: 1;
    overflow: hidden;
    min-height: 0;
}
.tab-panel-wrap {
    touch-action: none;
    display: flex;
    /*flex-direction: column;*/
    flex-shrink: 0;
    flex-grow: 1;
    overflow-x: hidden;
    /*position: relative;*/
    order: 1;
    width: 100%
}
.tab-panel {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    flex-grow: 1;
    width: 100%;
    /*overflow: auto;*/
}
.tab-bar {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    align-items: stretch;
    position: relative;
}
.tab-bar-content {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    align-items: stretch;
    min-height: 44px;
}
.tab-bar-bottom {
    display: flex;
    /*flex-direction: column;*/
    align-items: stretch;
    flex-shrink: 0;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    height: 50px;
    background: #fff;
    border-top: 1px solid #ddd;
    box-sizing: border-box;
}
.tab-bar-top {
    display: flex;
    align-items: stretch;
    flex-shrink: 0;
    position: fixed;
    left: 0;
    right: 0;
    background: #fff;
    margin-left: env(safe-area-inset-left);
    margin-right: env(safe-area-inset-right);
    box-sizing: border-box;
    min-height: 44px;
}
.tab-bar-tab {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-shrink: 1;
    flex-grow: 1;
    width: 100%
}
.tab-bar-tab-icon {
    display: flex;
    justify-content: center;
    width: 22px;
    height: 22px;
}
.tab-bar-tab-title {
    font-size: 10px;
    margin: 3px 0 0;
    line-height: 1;
    text-align: center
}
